.App {
    text-align: center;
}

.Zoo {}

.Zoo__item {
    font-size: larger;
    font-family: Arial, Helvetica, sans-serif;
    border: 1px;
    border-color: grey;
}

body {
    background: hsla(210, 20%, 90%, 1);
    line-height: 1.6;
    display: flex;
    justify-content: center;
    min-height: 100vh;
    font-family: 'Open Sans', sans-serif;
}

h1,
h2,
h3 {
    margin: 0;
}

h5 {
    font-family: 'Montserrat', sans-serif;
    font-size: 2em;
    margin-bottom: 0.2em;
}

.header {
    text-align: center;
    margin-bottom: 60px;
}

.header h1 {
    font-family: 'Montserrat', sans-serif;
    font-size: 3em;
    margin-bottom: 0.2em;
    line-height: 1.2;
    color: #222
}

.header h3 {
    font-weight: 400;
    color: #555;
    width: 30em;
}

.cards {
    display: flex;
    align-items: flex-start;
    justify-content: center;
}

.card {
    background: #fff;
    width: 16em;
    height: 20em;
    border-radius: 0.6em;
    margin: 1em;
    overflow: hidden;
    cursor: pointer;
    box-shadow: 0 13px 27px -5px hsla(240, 30.1%, 28%, 0.25), 0 8px 16px -8px hsla(0, 0%, 0%, 0.3), 0 -6px 16px -6px hsla(0, 0%, 0%, 0.03);
    transition: all ease 200ms;
}

.card:hover {
    transform: scale(1.03);
    box-shadow: 0 13px 40px -5px hsla(240, 30.1%, 28%, 0.12), 0 8px 32px -8px hsla(0, 0%, 0%, 0.14), 0 -6px 32px -6px hsla(0, 0%, 0%, 0.02);
}

.card img {
    width: 101%;
    object-fit: cover;
}

.card h2 {
    color: #222;
    margin-top: -0.2em;
    line-height: 1.4;
    font-size: 1.3em;
    font-weight: 500;
    font-family: 'Montserrat', sans-serif;
    transition: all ease-in 100ms;
}

.card p {
    color: #777;
}

.card h5 {
    color: rgb(0, 0, 0);
    font-weight: 700;
    font-size: 0.7em;
    letter-spacing: 0.04em;
    margin: 1.4em 0 0 0;
    text-transform: uppercase;
}

.card-body {
    padding: 1.2em;
}